"use client";

import { useState, Suspense } from "react";
import { use } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { ArrowLeft } from "lucide-react";
import Link from "next/link";
import toast, { Toaster } from "react-hot-toast";

import { QuestionDetail } from "@/components/qa/QuestionDetail";
import { AnswerList } from "@/components/qa/AnswerList";
import { AnswerEditor } from "@/components/qa/AnswerEditor";
import { RelatedQuestions } from "@/components/qa/RelatedQuestions";

// Mock data - this would normally come from an API
const mockQuestion = {
  id: 1,
  title: "How to implement WebSocket authentication in Next.js 13?",
  content:
    "I'm trying to implement real-time features in my Next.js application using WebSocket, but I'm struggling with the authentication part. I've tried several approaches but none seem to work properly with the Next.js 13 app router.",
  code: `
// Current implementation
const ws = new WebSocket('ws://localhost:3000');
ws.onopen = () => {
  // How to send auth token?
  ws.send(JSON.stringify({ token: 'xyz' }));
};`,
  language: "javascript",
  tags: ["next.js", "websocket", "authentication"],
  votes: 25,
  views: 342,
  answers: [
    {
      id: 1,
      content:
        "Here's how you can implement WebSocket authentication in Next.js 13...",
      code: `
// In your WebSocket server
import { verify } from 'jsonwebtoken';

wss.on('connection', (ws, req) => {
  ws.on('message', (message) => {
    const data = JSON.parse(message);
    if (data.type === 'auth') {
      try {
        const decoded = verify(data.token, process.env.JWT_SECRET);
        ws.user = decoded;
        ws.authorized = true;
      } catch (err) {
        ws.close();
      }
    }
  });
});

// In your client
const ws = new WebSocket('ws://localhost:3000');
ws.onopen = () => {
  ws.send(JSON.stringify({
    type: 'auth',
    token: localStorage.getItem('token')
  }));
};`,
      author: {
        name: "Ahmed Ben Mohamed",
        image: "/path/to/avatar.jpg",
        reputation: 3200,
      },
      votes: 42,
      isAccepted: true,
      createdAt: "2025-02-22T12:15:00",
      comments: [
        {
          id: 1,
          content: "This is exactly what I was looking for, thank you!",
          author: {
            name: "Malek Gharsallah",
            image: "/MALEK.jpg",
            reputation: 1250,
          },
          votes: 3,
          createdAt: "2025-02-22T12:30:00",
        },
      ],
    },
  ],
  solved: true,
  createdAt: "2025-02-22T10:30:00",
  author: {
    name: "Malek Gharsallah",
    image: "/MALEK.jpg",
    reputation: 1250,
  },
  comments: [
    {
      id: 1,
      content:
        "Have you considered using Socket.IO? It has built-in auth support.",
      author: {
        name: "Mohamed Salah",
        image: "/path/to/avatar.jpg",
        reputation: 890,
      },
      votes: 5,
      createdAt: "2025-02-22T11:00:00",
    },
  ],
};

function QuestionContent({ id }) {
  const [question, setQuestion] = useState(mockQuestion);
  const [isSubmittingAnswer, setIsSubmittingAnswer] = useState(false);

  const handleVote = (id, type, voteType) => {
    const newQuestion = { ...question };
    if (type === "question") {
      newQuestion.votes += voteType === "up" ? 1 : -1;
    } else {
      const answer = newQuestion.answers.find((a) => a.id === id);
      if (answer) {
        answer.votes += voteType === "up" ? 1 : -1;
      }
    }
    setQuestion(newQuestion);
    toast.success(
      `Vote ${voteType === "up" ? "upvoted" : "downvoted"} successfully`,
      {
        style: {
          background: "#363636",
          color: "#fff",
        },
      }
    );
  };

  const handleAnswerSubmit = (answerData) => {
    setIsSubmittingAnswer(true);
    // Simulate API call
    setTimeout(() => {
      const newAnswer = {
        id: question.answers.length + 1,
        content: answerData.content,
        code: answerData.code,
        author: {
          name: "Current User",
          image: "/path/to/avatar.jpg",
          reputation: 500,
        },
        votes: 0,
        isAccepted: false,
        createdAt: new Date().toISOString(),
        comments: [],
      };

      setQuestion((prev) => ({
        ...prev,
        answers: [...prev.answers, newAnswer],
      }));

      setIsSubmittingAnswer(false);
      toast.success("Answer posted successfully!", {
        style: {
          background: "#363636",
          color: "#fff",
        },
      });
    }, 1000);
  };

  const handleAcceptAnswer = (answerId) => {
    setQuestion((prev) => ({
      ...prev,
      solved: true,
      answers: prev.answers.map((answer) => ({
        ...answer,
        isAccepted: answer.id === answerId,
      })),
    }));
    toast.success("Answer marked as accepted!", {
      style: {
        background: "#363636",
        color: "#fff",
      },
    });
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center gap-4">
        <Button
          variant="ghost"
          size="sm"
          asChild
          className="hover:bg-slate-100 dark:hover:bg-slate-800"
        >
          <Link href="/qa">
            <ArrowLeft className="h-4 w-4 mr-2" />
            Back to Questions
          </Link>
        </Button>
      </div>

      <div className="grid gap-6 md:grid-cols-12">
        {/* Main content */}
        <div className="md:col-span-9 space-y-6">
          <QuestionDetail
            question={question}
            onVote={handleVote}
            onAcceptAnswer={handleAcceptAnswer}
          />

          <Card className="p-6">
            <h2 className="text-2xl font-semibold mb-6">
              {question.answers.length}{" "}
              {question.answers.length === 1 ? "Answer" : "Answers"}
            </h2>
            <AnswerList
              answers={question.answers}
              onVote={handleVote}
              onAcceptAnswer={handleAcceptAnswer}
              isAuthor={true}
            />
          </Card>

          <Card className="p-6">
            <h2 className="text-2xl font-semibold mb-6">Your Answer</h2>
            <AnswerEditor
              onSubmit={handleAnswerSubmit}
              isSubmitting={isSubmittingAnswer}
            />
          </Card>
        </div>

        {/* Sidebar */}
        <div className="md:col-span-3">
          <RelatedQuestions questionId={id} />
        </div>
      </div>

      <Toaster position="top-right" />
    </div>
  );
}

export default function QuestionDetailPage({ params }) {
  const id = use(params).id;

  return (
    <Suspense fallback={<div>Loading...</div>}>
      <QuestionContent id={id} />
    </Suspense>
  );
}
