// src/components/qa/AnswerEditor.jsx
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/esm/styles/prism";

const programmingLanguages = [
  "javascript",
  "python",
  "java",
  "c++",
  "ruby",
  "go",
  "rust",
  "typescript",
  "php",
  "swift",
];

export function AnswerEditor({ onSubmit, isSubmitting }) {
  const [formData, setFormData] = useState({
    content: "",
    code: "",
    language: "javascript",
  });
  const [activeTab, setActiveTab] = useState("write");

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
    setFormData({
      content: "",
      code: "",
      language: "javascript",
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <Tabs
        value={activeTab}
        onValueChange={setActiveTab}
        className="space-y-4"
      >
        <TabsList>
          <TabsTrigger value="write">Write</TabsTrigger>
          <TabsTrigger value="preview">Preview</TabsTrigger>
        </TabsList>

        <TabsContent value="write" className="space-y-4">
          <Textarea
            placeholder="Write your answer here..."
            className="min-h-[200px]"
            value={formData.content}
            onChange={(e) =>
              setFormData({ ...formData, content: e.target.value })
            }
          />

          <div className="space-y-4">
            <div className="flex items-center gap-4">
              <Select
                value={formData.language}
                onValueChange={(value) =>
                  setFormData({ ...formData, language: value })
                }
              >
                <SelectTrigger className="w-[200px]">
                  <SelectValue placeholder="Select language" />
                </SelectTrigger>
                <SelectContent>
                  {programmingLanguages.map((lang) => (
                    <SelectItem key={lang} value={lang}>
                      {lang}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <Textarea
              placeholder="Add code example (optional)"
              className="font-mono"
              value={formData.code}
              onChange={(e) =>
                setFormData({ ...formData, code: e.target.value })
              }
            />
          </div>
        </TabsContent>

        <TabsContent value="preview" className="min-h-[200px]">
          <div className="prose prose-slate dark:prose-invert max-w-none">
            <p>{formData.content}</p>
            {formData.code && (
              <SyntaxHighlighter
                language={formData.language}
                style={vscDarkPlus}
                className="!bg-slate-900 rounded-lg"
                showLineNumbers
              >
                {formData.code.trim()}
              </SyntaxHighlighter>
            )}
          </div>
        </TabsContent>
      </Tabs>

      <div className="flex items-center gap-4">
        <Button
          type="submit"
          className="bg-gradient-to-r from-red-800 to-red-600 text-white"
          disabled={!formData.content.trim() || isSubmitting}
        >
          {isSubmitting ? "Posting..." : "Post Your Answer"}
        </Button>
        <p className="text-sm text-muted-foreground">
          Remember to be respectful and provide detailed explanations.
        </p>
      </div>
    </form>
  );
}