// src/components/qa/AnswerList.jsx
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { format } from "date-fns";
import { ArrowUp, ArrowDown, CheckCircle2 } from "lucide-react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/esm/styles/prism";

export function AnswerList({ answers, onVote, onAcceptAnswer, isAuthor }) {
  const sortedAnswers = [...answers].sort((a, b) => {
    // Accepted answer always first
    if (a.isAccepted) return -1;
    if (b.isAccepted) return 1;
    // Then sort by votes
    return b.votes - a.votes;
  });

  return (
    <div className="space-y-6">
      {sortedAnswers.map((answer) => (
        <div key={answer.id} className="border-b last:border-0 pb-6 last:pb-0">
          <div className="flex gap-6">
            {/* Voting */}
            <div className="flex flex-col items-center gap-2">
              <Button
                variant="ghost"
                size="sm"
                className="hover:bg-blue-50 dark:hover:bg-blue-900/50 text-blue-600 dark:text-blue-400"
                onClick={() => onVote(answer.id, "answer", "up")}
              >
                <ArrowUp className="h-6 w-6" />
              </Button>
              <span className="text-xl font-semibold">{answer.votes}</span>
              <Button
                variant="ghost"
                size="sm"
                className="hover:bg-red-50 dark:hover:bg-red-900/50 text-red-600 dark:text-red-400"
                onClick={() => onVote(answer.id, "answer", "down")}
              >
                <ArrowDown className="h-6 w-6" />
              </Button>
              {isAuthor && !answer.isAccepted && (
                <Button
                  variant="outline"
                  size="sm"
                  className="mt-2 text-green-600 dark:text-green-400 hover:bg-green-50 dark:hover:bg-green-900/50"
                  onClick={() => onAcceptAnswer(answer.id)}
                >
                  <CheckCircle2 className="h-5 w-5" />
                </Button>
              )}
              {answer.isAccepted && (
                <div className="mt-2 text-green-600 dark:text-green-400">
                  <CheckCircle2 className="h-5 w-5 fill-current" />
                </div>
              )}
            </div>

            {/* Content */}
            <div className="flex-1 space-y-4">
              <div className="prose prose-slate dark:prose-invert max-w-none">
                <p>{answer.content}</p>
                {answer.code && (
                  <SyntaxHighlighter
                    language="javascript"
                    style={vscDarkPlus}
                    className="!bg-slate-900 rounded-lg"
                    showLineNumbers
                  >
                    {answer.code.trim()}
                  </SyntaxHighlighter>
                )}
              </div>

              {/* Author and Timestamp */}
              <div className="flex items-center justify-between pt-4">
                <div className="flex items-center gap-4">
                  <Avatar className="h-10 w-10">
                    <AvatarImage src={answer.author.image} />
                    <AvatarFallback>
                      {answer.author.name
                        .split(" ")
                        .map((n) => n[0])
                        .join("")}
                    </AvatarFallback>
                  </Avatar>
                  <div>
                    <p className="font-medium">{answer.author.name}</p>
                    <p className="text-sm text-muted-foreground">
                      {answer.author.reputation.toLocaleString()} reputation •{" "}
                      answered{" "}
                      {format(
                        new Date(answer.createdAt),
                        "MMM d, yyyy 'at' h:mm a"
                      )}
                    </p>
                  </div>
                </div>
                <Button variant="ghost" size="sm">
                  Share
                </Button>
              </div>

              {/* Comments */}
              {answer.comments && answer.comments.length > 0 && (
                <div className="pt-4 space-y-3">
                  {answer.comments.map((comment) => (
                    <div
                      key={comment.id}
                      className="flex items-start gap-3 text-sm"
                    >
                      <Button variant="ghost" size="sm" className="px-1">
                        <ArrowUp className="h-4 w-4" />
                      </Button>
                      <span className="font-medium">{comment.votes}</span>
                      <div className="flex-1">
                        <p>{comment.content}</p>
                        <div className="flex items-center gap-2 mt-1 text-muted-foreground">
                          <span>{comment.author.name}</span>
                          <span>•</span>
                          <span>
                            {format(new Date(comment.createdAt), "MMM d, yyyy")}
                          </span>
                        </div>
                      </div>
                    </div>
                  ))}
                  <Button
                    variant="ghost"
                    size="sm"
                    className="text-green-600 dark:text-green-400"
                  >
                    Add a comment
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}