// src/components/qa/QuestionDetail.jsx
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { format } from "date-fns";
import {
  ArrowUp,
  ArrowDown,
  MessageSquare,
  Eye,
  CheckCircle2,
} from "lucide-react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/esm/styles/prism";

export function QuestionDetail({ question, onVote }) {
  return (
    <Card className="p-6">
      <div className="flex gap-6">
        {/* Voting */}
        <div className="flex flex-col items-center gap-2">
          <Button
            variant="ghost"
            size="sm"
            className="hover:bg-red-50 dark:hover:bg-red-900/50 text-red-600 dark:text-red-400"
            onClick={() => onVote(question.id, "question", "up")}
          >
            <ArrowUp className="h-6 w-6" />
          </Button>
          <span className="text-xl font-semibold">{question.votes}</span>
          <Button
            variant="ghost"
            size="sm"
            className="hover:bg-red-50 dark:hover:bg-red-900/50 text-red-600 dark:text-red-400"
            onClick={() => onVote(question.id, "question", "down")}
          >
            <ArrowDown className="h-6 w-6" />
          </Button>
        </div>

        {/* Main Content */}
        <div className="flex-1 space-y-6">
          {/* Header */}
          <div>
            <div className="flex items-start justify-between gap-4">
              <h1 className="text-2xl font-bold">{question.title}</h1>
              {question.solved && (
                <Badge
                  variant="success"
                  className="bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-100"
                >
                  <CheckCircle2 className="h-4 w-4 mr-1" />
                  Solved
                </Badge>
              )}
            </div>
            <div className="flex items-center gap-4 mt-2 text-sm text-muted-foreground">
              <span>
                Asked{" "}
                {format(
                  new Date(question.createdAt),
                  "MMM d, yyyy 'at' h:mm a"
                )}
              </span>
              <span className="flex items-center gap-1">
                <Eye className="h-4 w-4" /> {question.views} views
              </span>
            </div>
          </div>

          {/* Question Content */}
          <div className="prose prose-slate dark:prose-invert max-w-none">
            <p>{question.content}</p>
            {question.code && (
              <SyntaxHighlighter
                language={question.language}
                style={vscDarkPlus}
                className="!bg-slate-900 rounded-lg"
                showLineNumbers
              >
                {question.code.trim()}
              </SyntaxHighlighter>
            )}
          </div>

          {/* Tags */}
          <div className="flex flex-wrap gap-2">
            {question.tags.map((tag) => (
              <Badge
                key={tag}
                variant="secondary"
                className="bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100"
              >
                {tag}
              </Badge>
            ))}
          </div>

          {/* Author and Share */}
          <div className="flex items-center justify-between pt-4 border-t">
            <div className="flex items-center gap-4">
              <Avatar className="h-12 w-12">
                <AvatarImage src={question.author.image} />
                <AvatarFallback>
                  {question.author.name
                    .split(" ")
                    .map((n) => n[0])
                    .join("")}
                </AvatarFallback>
              </Avatar>
              <div>
                <p className="font-medium">{question.author.name}</p>
                <p className="text-sm text-muted-foreground">
                  {question.author.reputation.toLocaleString()} reputation
                </p>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <Button variant="outline" size="sm">
                Share
              </Button>
              <Button variant="outline" size="sm">
                Follow
              </Button>
            </div>
          </div>

          {/* Comments */}
          {question.comments && question.comments.length > 0 && (
            <div className="border-t pt-4 space-y-4">
              {question.comments.map((comment) => (
                <div
                  key={comment.id}
                  className="flex items-start gap-3 text-sm"
                >
                  <Button variant="ghost" size="sm" className="px-1">
                    <ArrowUp className="h-4 w-4" />
                  </Button>
                  <span className="font-medium">{comment.votes}</span>
                  <div className="flex-1">
                    <p>{comment.content}</p>
                    <div className="flex items-center gap-2 mt-1 text-muted-foreground">
                      <span>{comment.author.name}</span>
                      <span>•</span>
                      <span>
                        {format(new Date(comment.createdAt), "MMM d, yyyy")}
                      </span>
                    </div>
                  </div>
                </div>
              ))}
              <Button
                variant="ghost"
                size="sm"
                className="text-green-600 dark:text-green-400"
              >
                Add a comment
              </Button>
            </div>
          )}
        </div>
      </div>
    </Card>
  );
}